.mainCont,
.ansReview {
  background-color: #fff;
  padding: 50px;
  text-align: center;
  border: 1px solid #000;
  box-shadow: 2px 6px 6px #000;
  margin: 50px 0;
}

.ansReviewList {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  grid-auto-flow: column;
}

.recordContainer {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  /* max-height: 890px;
  overflow: scroll; */
}

/* .recordContainer span {
  padding: 5px;
  width: 15%;
  display: inline-block;

  border: 1px solid #555;
} */

.recordContainer span {
  padding: 8px;
  width: 12%;
  display: inline-block;
  /* border: 1px solid #555; */
}

.recordContainer .dateBtn {
  background-color: #008000;
  color: #fff;
  padding: 5px;
  font-size: 18px;
  border-radius: 5px;
  cursor: pointer;
  text-transform: uppercase;
}

.record {
  text-transform: uppercase;
}

.hdr {
  font-size: 4rem;
}

.correct {
  color: green;
}

.wrong {
  color: #f00;
}

.ansReview > span {
  font-size: 26px;
  display: inline-block;
  margin-right: 1.5rem;
}

.ansReview .result span {
  padding: 0.2rem;
  display: inline-block;
}

.resultCont {
  display: flex;
  flex-wrap: wrap;
}

.resultCont .result {
  width: 25%;
}

.ansReviewList {
  text-align: left;
}

.ansReview .qn {
  color: gray;
}

.hdrRow {
  /* font-weight: bold; */
  /* text-transform: capitalize; */
}

/* .altRow {
  background-color: rgb(247, 242, 242);
} */

.records span {
  background-color: #000;
  color: #fff;
}

.altRow span {
  background-color: #111;
}

.current {
  white-space: pre-line;
}

.cap {
  text-transform: capitalize;
}

.allTotal {
  color: gray;
}

.cutOff {
  border-top: 2px solid red;
  color: grey !important;
}

.cutOf50 span {
  color: grey;
  /* font-size: 14px; */
}

.current span {
  background-color: #008000;
  color: #000;
}

.today {
  color: blue;
}

.btn {
  background-color: #008000;
  color: #fff;
  padding: 10px;
  font-size: 40px;
  margin: 0;
  border-radius: 15px;
  margin-left: 20px;
  cursor: pointer;
  margin: 10px 0;
}

.firstPlace {
  width: 60px;
}

.champ span {
  background-color: gold;
  color: #000;
  font-weight: bold;
  font-size: 18px;
}

.pyro > .before,
